.loading-transition { 
  width: 200px;
  margin: 0 auto;
  border-radius: 25px;
  border: 0px solid transparent;
  position: relative;
  padding: 1px;
}
.loading-transition:before {
  content:'';
  border:1px solid transparent; 
  border-radius:10px;
  position:absolute;
  top:0px; 
  right:0px; 
  bottom:-4px; 
  left:0px;
  background-color: #f2f2f2;
}
.loading-transition .loaderBar { 
    position: absolute;
    border-radius: 10px;
    top: 0;
    right: 100%;
    bottom: 0;
    left: 0;
    background: #3766e8;
    width: 100%;
    height: 6px;
    animation: borealisBar 0.8s linear infinite;
}

@keyframes borealisBar {
  0% {
    left:0%;
    right:100%;
    width:0%;
  }
  10% {
    left:0%;
    right:75%;
    width:25%;
  }
  90% {
    right:0%;
    left:75%;
    width:25%;
  }
  100% {
    left:100%;
    right:0%;
    width:0%;
  }
}